<template>
  <div>
    <el-form :model="parmes" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" required>
        <el-button size="mini" icon="el-icon-plus" @click="dialogFormVisible = true"></el-button>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="parmes.backgroundColor"></el-color-picker>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="parmes.textColor"></el-color-picker>
      </el-form-item>
    </el-form>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible" class="table-dialog" width="900px">
      <div slot="footer" class="dialog-footer">
        <el-button type="primary"> </el-button>
        <el-button @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
    <!--  end -->
  </div>
</template>

<script>
  export default {
    name: 'notice',
    props: ['parmes'],
    data() {
      return {
        dialogFormVisible: false, // 
        noticeSearchTxt: '' // 
      }
    }
  }
</script>

<style scoped>

</style>



